<style lang="less" rel="stylesheet/less" scoped>
  @import '../css/theme';
  .projects-page{
    padding-top: 60px;
    & > div{
      background-color: #fff;
      width:90%;
      padding: 10px 15px;
      margin: 0 auto;
      margin-top: 60px;
      border-radius: 4px;
      & > header{
        padding-bottom: 20px;
        border-bottom: 1px solid #ccc;
        font-weight: 600;
        box-sizing: border-box;
        span{
          padding: 15px 8px;
          cursor: pointer;
        }
      }
      .projects-container{
        padding: 30px 0;
        box-sizing: border-box;
      }
    }
  }
  .clicked{
    border-bottom: 2px solid @secondColor;
  }
</style>
<template>
  <div class="projects-page">
    <div>
      <header @click="selectType">
        <span data-type="releasing" class="clicked"></span>
        <span data-type="applying"></span>
        <span></span>
        <span></span>
      </header>
      <div class="projects-container row">
        <router-link
          v-for="item in showList"
          :to="{name: 'showReleasePro', params: {id: item.id}}"
          :key="item.id">
          <project-card :item="item"></project-card>
        </router-link>
      </div>
    </div>
  </div>
</template>
<script>
  import ProjectCard from '@/components/share/ProjectCard'
  import {baseUrl} from '@/config/config'
  export default {
    data () {
      return {
        showList: []
      }
    },
    beforeRouteEnter (to, from, next) {
      next(vm => {
        if (!vm.$store.state.hasLogin) {
          alert('')
          vm.$router.push({name: 'index'})
        }
        vm.getTypeData('releasing')
        window.onscroll = function () {}
        vm.$store.commit('changeSingerState', {stateName: 'myHeader', value: true})
      })
    },
    methods: {
      selectType (e) {
        if (e.target.nodeName === 'SPAN') {
          switch(e.target.getAttribute('data-type'))
          {
            case 'releasing':
              this.getTypeData('releasing')
              break;
            case 'applying':
              this.getApplyList()
              break

          }
        }
    },
      getApplyList () {
        this.$ajax.get(baseUrl + 'applyListShow', {
          params: {
            applyUserId: this.$store.state.loginId,
          }
        }).then(res => {
          this.showList = res.data
        }, res => {
          alert(',')
        })
      },
      getTypeData (type) {
        let store = window.localStorage
        if (store['token']) {
          this.$ajax.get(baseUrl + 'myProjectData', {
            params: {
              id: this.$store.state.loginId,
              type: type
            }
          })
            .then(res => {
              this.showList = res.data
            }, res => {
              alert('')
            })
        }
      }
    },
    components: {
      ProjectCard
    }
  }
</script>




