<style lang="less" rel="stylesheet/less" scoped>
  @import '../css/theme.less';
  #login-dialog{
    .my-dialog(35%)
  }
  .loginBtn div > div,.loginBtn div >input{
    .btnTheme
  }
</style>
<template>
  <div id="login-dialog" class="col-sm-5">
    <form class="form-horizontal">
      <div class="form-group">
        <label for="accout" class="col-sm-2 control-label">:</label>
        <div class="col-sm-8">
          <input v-model="userName" type="text" id="accout" class="form-control" placeholder="/" name="username">
        </div>
      </div>
      <div class="form-group">
        <label for="psw" class="col-sm-2 control-label">:</label>
        <div class="col-sm-8">
          <input v-model="password" type="password" id="psw" class="form-control" placeholder="" name="password">
        </div>
      </div>
      <div class="form-group">
        <div class="col-sm-offset-2 col-sm-10">
          <div class="checkbox">
            <label>
              <input type="checkbox"> 
            </label>
          </div>
        </div>
      </div>
      <div class="form-group loginBtn">
        <div class="col-sm-offset-2 col-sm-10">
          <input type="button" value="" @click="submit">
          <div @click="cancel">
            
          </div>
        </div>
      </div>
    </form>
  </div>
</template>
<script>
  import {baseUrl} from '@/config/config'
  import sha1 from 'js-sha1'
  export default {
    name: 'Login',
    data () {
      return {
        userName: '',
        password: ''
      }
    },
    methods: {
      cancel () {
        this.$emit('next', 'showLogin')
      },
      submit () {
        this.$http.post(baseUrl + 'login', {
          userName: this.userName,
          password: sha1(this.password)
        }, {emulateJSON: true}).then(res => {
//          todo 
          if (res.data !== 'fail') {
            let store = window.localStorage
            store['token'] = res.data
            this.$store.commit('changeLoginId', store['token'].split('-')[0])
            this.cancel()
            this.$store.commit('changeSingerState', {stateName: 'hasLogin', value: true})
            return this.$ajax.get(baseUrl + 'showBase', {
              params: {
                id: store['token'].split('-')[0]
              }
            })
          } else {
//            todo
            console.log('')
          }
        }, res => {
//          todo
          console.log('')
        }).then(
          res => {
            this.$store.commit('changeHead', res.data.headImg)
          }, res => {
            alert('')
          }
        )
      }
    }
  }
</script>




