<style lang="less" rel="stylesheet/less" scoped>
  @import '../css/theme.less';
  #dialog{
    .my-dialog(35%);
  }
  .registerBtn div > div, .registerBtn div >input{
    .btnTheme
  }
  .form-control{
    border: 0;
    border-bottom: 1px solid #e6e6e6;
    box-shadow: none;
  }
</style>
<template>
  <div id="dialog" class="col-sm-5">
    <form class="form-horizontal">
      <div class="form-group">
        <div class="col-sm-offset-2 col-sm-8">
          <input v-model="phone" type="text" id="phone" class="form-control" placeholder="" name="phone">
        </div>
      </div>
      <div class="form-group">
        <div class="col-sm-offset-2 col-sm-8">
          <input v-model="userName" type="text" id='username' class="form-control " placeholder="" name="username">
        </div>
      </div>
      <div class="form-group">
        <div class="col-sm-offset-2 col-sm-8">
          <input v-model="password" type="password" class="form-control psw" placeholder="" name="password">
        </div>
      </div>
      <div class="form-group">
        <div class="col-sm-offset-2 col-sm-8">
          <input v-model="password2" type="password" class="form-control psw" placeholder="" name="password2">
        </div>
      </div>
      <div class="form-group registerBtn">
        <div class="col-sm-offset-2 col-sm-10">
          <input type="button" value="" @click="submit">
          <div @click="cancel">
            
          </div>
        </div>
      </div>
    </form>
  </div>
</template>
<script>
  import {baseUrl} from '@/config/config'
  import sha1 from 'js-sha1'
  export default {
    name: 'Register',
    data () {
      return {
        userName: '',
        phone: '',
        password: '',
        password2: ''
      }
    },
    methods: {
      cancel () {
        this.$emit('next', 'showRegister')
      },
      submit () {
        this.$http.post(baseUrl + 'register', {
          phone: this.phone,
          userName: this.userName,
          password: sha1(this.password)
        }, {emulateJSON: true}).then(res => {
          console.log(res.data)
          if (res.data === '1') {
            if (confirm('!')) {
              this.cancel()
              this.$emit('next', 'showLogin')
            }
          }
        }, res => {
          console.log(res.data)
        })
      }
    }
  }
</script>




